/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    .qodef-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        border-bottom: 1px solid $default-heading-color;

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            text-align: center;
            border: 0;
            box-sizing: border-box;
        }

        .qodef-quantity-input {
            position: relative;
            height: 58px;
            width: 54px;
            padding: 0;
            margin: 0 12px 0 0;
            font-family: inherit;
	        font-size: 16px;
            line-height: 56px;
            outline: 0;
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            position: absolute;
            right: 0;
            width: 18px;
            height: 18px;
	        font-size: 12px;
            line-height: 29px;
	        text-align: right;
            cursor: pointer;
            @include qodefTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-quantity-plus {
            top: 5px;
        }

        .qodef-quantity-minus {
            bottom: 15px;
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */