/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.qodef-plc-holder {
    @include qodefRelativeHolderLayout();

    .qodef-plc-outer {
        @include qodefRelativeHolderLayout();
    }

    .qodef-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        &:hover {

            .qodef-plc-image-outer {

                .qodef-plc-text {
                    opacity: 1;
                }
            }
        }

        .touch & {
            cursor: pointer;
        }

        .qodef-plc-image-outer {
            @include qodefRelativeHolderLayout();

            .qodef-plc-image {
                @include qodefRelativeHolderLayout();

                .qodef-plc-onsale,
                .qodef-plc-out-of-stock,
                .qodef-plc-new-product {
	                @include qodefWooCommerceMarksGeneralStyle();
                }

                .qodef-plc-onsale {
	                @include qodefWooCommerceSaleStyle();
                }

                .qodef-plc-out-of-stock {
	                @include qodefWooCommerceOutOfStockStyle();
                }
	
	            .qodef-plc-new-product {
		            @include qodefWooCommerceNewProductStyle();
	            }
            }
	
	        .qodef-plc-text {
		        position: absolute;
		        height: auto;
		        top: auto;
		        bottom: 0;
		        width: 100%;
		        opacity: 0;
		        background-color: transparent;
		        @include qodefTransition(opacity .3s ease-in-out)
	        }
	
	        .qodef-plc-text-outer {
		        position: relative;
		        display: table;
		        width: 100%;
		        height: 100%;
		        text-align: center;
	        }
	
	        .qodef-plc-text-inner {
		        display: block;
		        width: auto;
		        height: auto;
		        text-align: center;
		        background-color: #fff;
		        box-shadow: 0px 3px 5px 1px rgba(25, 25, 25, 0.1);
		        padding: 29px 0 19px;
		        margin: 20px;
	        }
        }

        .qodef-plc-title {
            margin: 0 0 4px;
        }
	
	    .qodef-plc-category {
		    margin: 0 0 7px;
		    line-height: 1em;
	    }

        .qodef-plc-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 11px 0 3px;
        }

        .qodef-plc-rating-holder {
            @include qodefRelativeHolderLayout();
            line-height: 1;
            margin: 0 0 12px;
        }

        .qodef-plc-rating {
	        @include qodefWooCommerceRatingsStyle();
	        
        }
	
	    .qodef-plc-price {
		    position: absolute;
		    display: block;
		    top: 18px;
		    right: 20px;
		    font-size: 16px;
		    font-weight: 700;
		    line-height: 1;
		    color: $default-heading-color;
		    margin: 0;
		
		    * {
			    font-size: inherit;
			    line-height: inherit;
		    }
		
		    del {
			    margin: 0 4px 0 0;
		    }
		
		    ins {
			    color: $first-main-color;
			    text-decoration: none;
		    }
	    }
	
	    .button,
	    .added_to_cart {
		    @include qodefButtonDefaultStyle();
		    width: 100%;
		    white-space: nowrap;
		    cursor: pointer;
		    padding: 5px 15px;
		    margin: 0;
		    color: $default-heading-color;
		    background-color: transparent;
		    border: 0;
		    vertical-align: middle;
		    text-align: center;
		    z-index: 3;
		
		    &:hover {
			    color: $first-main-color;
		    }
	    }
	
	    .button,
	    .added_to_cart {
		    @include qodefButtonDefaultStyle();
		    @include qodefButtonSize();
		    white-space: nowrap;
		    z-index: 3;
	    }
	
	    .button.added {
		    display: none;
	    }
	
	    .qodef-plc-add-to-cart {
		    margin: 20px 0 0;
		
		    &.qodef-default-skin {
			
			    .button,
			    .added_to_cart {
				    @include qodefButtonSolidColor();
				
				    &:hover {
					    @include qodefButtonSolidHoverColor();
				    }
			    }
		    }
		
		    &.qodef-light-skin {
			
			    .button,
			    .added_to_cart {
				    color: $default-heading-color;
				    background-color: #fff;
				
				    &:hover {
					    color: #fff;
					    background-color: $additional-color;
				    }
			    }
		    }
		
		    &.qodef-dark-skin {
			
			    .button,
			    .added_to_cart {
				    color: #fff;
				    background-color: $default-heading-color;
				
				    &:hover {
					    color: #fff;
					    background-color: $additional-color;
				    }
			    }
		    }
	    }

        .qodef-plc-link {
	        @include qodefAbsoluteHolderLayout();
	        position: relative;
        }
    }
	
	&.qodef-standard-layout {
		
		.qodef-plc-item {
			
			&:hover {
				
				.qodef-pl-text {
					opacity: 1;
				}
			}
			
			.qodef-plc-rating {
				color: $first-main-color;
			}
			
			.qodef-plc-title,
			.qodef-plc-category a,
			.qodef-plc-excerpt,
			.qodef-plc-.qodef-plc-price {
				color: $default-heading-color;
			}
		}
		
		.qodef-plc-add-to-cart {
			margin: 0;
		}
	}

    &.qodef-simple-layout {
        
        .qodef-plc-image-outer {
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 15px 0 0;
        }

        .qodef-plc-text-wrapper {
            position: relative;
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 0 0 15px;
        }

        .qodef-plc-text,
        .qodef-plc-text-outer,
        .qodef-plc-text-inner {
            @include qodefRelativeHolderLayout();
            -webkit-transform: translateZ(0);
        }
	
	    .qodef-plc-text {
		    position: absolute;
		    height: auto;
		    top: auto;
		    bottom: 0;
		    width: 100%;
		    opacity: 0;
		    background-color: transparent;
		    @include qodefTransition(opacity .3s ease-in-out)
	    }
	
	    .qodef-plc-text-outer {
		    position: relative;
		    display: table;
		    width: 100%;
		    height: 100%;
		    text-align: center;
	    }
	
	    .qodef-plc-text-inner {
		    display: block;
		    width: auto;
		    height: auto;
		    background-color: #fff;
		    box-shadow: 0px 3px 5px 1px rgba(25, 25, 25, 0.1);
		    padding: 22px 0 13px;
		    margin: 19px 17px;
	    }
    }
	
	&.qodef-plc-nav-light-skin {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				color: $header-light-color;
				
				&:hover {
					color: $header-light-hover-color;
				}
			}
		}
	}
	
	&.qodef-plc-pag-inside-slider {
		
		.owl-prev,
		.owl-next {
			@include qodefTransform(translateY(-50%));
		}
		
		.owl-dots {
			position: absolute;
			left: 0;
			bottom: 10px;
			margin: 0;
		}
	}
	
	&.qodef-plc-pag-light-skin {

		.owl-nav {

			.owl-prev,
			.owl-next {
				color: $header-light-color;

				&:hover {
					color: $header-light-hover-color;
				}
			}
		}

		.owl-dots {
			
			.owl-dot {

				span {
					background-color: $header-light-hover-color;
				}

				&:hover,
				&.active {

					span {
						background-color: $header-light-color;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */