/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.qodef-content .woocommerce {
    @include qodefRelativeHolderLayout();
}

.qodef-pl-main-holder {
    margin: 0 0 22px;
    
    .qodef-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .qodef-pl-inner .qodef-pl-text {
                opacity: 1;
            }
        }
        
        .qodef-product-list-title {
            margin: 0 0 2px;
        }
        
        .qodef-pl-rating-holder {
            @include qodefRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }
        
        .price {
            @include qodefWooCommercePriceStyle();
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .qodef-woo-pl-info-below-image & {
                margin: 0;
            }
        }
	    
        .button.added {
            display: none;
        }
        
        .qodef-pl-inner {
            @include qodefRelativeHolderLayout();
            overflow: hidden;
            
            .qodef-pl-image {
                @include qodefRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .qodef-pl-text {
                @include qodefAbsoluteHolderLayout();
                padding: 10px;
                opacity: 0;
                background-color: rgba($default-heading-color, 0.6);
                box-sizing: border-box;
                @include qodefTransition(opacity .2s ease-out);
            }
            
            .qodef-pl-text-outer {
                @include qodefTableLayout();
                text-align: center;
            }
            
            .qodef-pl-text-inner {
                @include qodefTableCellLayout();
                
                .qodef-product-list-title {
                    color: $default-heading-color;
                }
	            
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include qodefAbsoluteHolderLayout();
            }
        }
        
        .qodef-pl-text-wrapper {
            @include qodefRelativeHolderLayout();
            margin: 12px 0 18px;
        }
	
	    .qodef-woo-pl-info-on-image-hover & {
		
		    .qodef-pl-inner {
		        > a {
			        position: relative;
		        }
		    }
		
		    .price {
			    position: absolute;
			    top: 18px;
			    right: 20px;
			    margin: 0;
		    }
		
		    .qodef-pl-text {
			    position: absolute;
			    width: 100%;
			    height: auto;
			    top: auto;
			    bottom: 0;
			    opacity: 0;
			    background-color: transparent;
			    @include qodefTransition(opacity .3s ease-in-out);
			
			    .qodef-pl-text-outer {
				    position: relative;
				    display: block;
				    height: auto;
			    }
			
			    .qodef-pl-text-inner {
				    display: block;
				    width: auto;
				    height: auto;
				    text-align: center;
				    background-color: #fff;
				    box-shadow: 0px 3px 5px 1px rgba(25, 25, 25, 0.1);
				    padding: 29px 0 14px;
				    margin: 10px;
				
				    .qodef-pl-title {
					    margin: 0;
				    }
				
				    .qodef-pl-rating-holder {
					    margin: 0 0 2px;
				    }
				
				    .button,
				    .added_to_cart {
					    @include qodefButtonDefaultStyle();
					    white-space: nowrap;
					    cursor: pointer;
					    padding: 0 15px;
					    margin: -10px 0 0;
					    font-size: 13px;
					    color: $default-heading-color;
					    background-color: transparent;
					    border: 0;
					    vertical-align: middle;
					    text-align: center;
					    letter-spacing: .05em;
					    z-index: 3;

					    &:before {
                            content: '';
                            position: absolute;
                            top: calc(50% - 2px);
                            left: 8px;
                            height: 4px;
                            width: calc(100% - 16px);
                            background-color: $additional-color;
                            z-index: -1;
                            @include qodefTransitionTransform(.5s cubic-bezier(.23,1,.32,1));
                            @include qodefTransform(scaleX(0) translateZ(0));
                            @include qodefTransformOrigin(100% 50%);
                        }
					
					    &:hover {

						    &:before {
                                @include qodefTransformOrigin(0% 50%);
                                @include qodefTransform(scaleX(1) translateZ(0));
                            }
					    }
				    }
				
				    .button.added {
					    display: none;
				    }
			    }
			
			    .qodef-pli-title,
			    .qodef-pli-category a,
			    .qodef-pli-excerpt,
			    .qodef-pli-rating,
			    .qodef-pli-price {
				    color: $default-heading-color;
			    }
		    }
	    }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.qodef-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .qodef-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */