/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();

    .qodef-pli {
        padding: 0;
        z-index: 100;

	    .qodef-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .qodef-pli-category {
		    margin: 6px 0 3px;
	    }

        .qodef-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .qodef-pli-rating-holder {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
		    margin-right: -9px;
	        letter-spacing: 9px;
	    }

        .qodef-pli-price {
	        @include qodefWooCommercePriceStyle();
	        margin: 14px 0 0;
        }
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .qodef-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
            @include qodefRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .qodef-pli-onsale,
            .qodef-pli-out-of-stock,
            .qodef-pli-new-product {
	            @include qodefWooCommerceMarksGeneralStyle();
            }

            .qodef-pli-onsale {
	            @include qodefWooCommerceSaleStyle();
            }

            .qodef-pli-out-of-stock {
	            @include qodefWooCommerceOutOfStockStyle();
            }
	
	        .qodef-pli-new-product {
		        @include qodefWooCommerceNewProductStyle();
	        }
        }
            
        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            background-color: rgba($default-heading-color, 0.6);
            box-sizing: border-box;
            @include qodefTransition(opacity .2s ease-out);
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();

            .qodef-pli-title,
            .qodef-pli-category a,
            .qodef-pli-excerpt,
            .qodef-pli-rating,
            .qodef-pli-price {
                color: #fff;
            }
	
	        .qodef-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

            .button.added {
                display: none;
            }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
		    position: relative;
	    }
    }

    .qodef-pli-text-wrapper {
        @include qodefRelativeHolderLayout();
        margin: 12px 0 0;
    }
	
	&.qodef-info-on-image {
		
		.qodef-pli-price {
			position: absolute;
			top: 18px;
			right: 20px;
			margin: 0;
		}
		
		.qodef-pli-text {
			height: auto;
			top: auto;
			bottom: 0;
			opacity: 0;
			background-color: transparent;
			@include qodefTransition(opacity .3s ease-in-out);
			
			.qodef-pli-text-inner {
				display: block;
				width: auto;
				height: auto;
				background-color: #fff;
				box-shadow: 0px 3px 5px 1px rgba(25, 25, 25, 0.1);
				padding: 29px 0 19px;
				margin: 10px;
				
				.qodef-pli-title {
					margin: 0 0 2px;
				}
				
				p {
					
					&.qodef-pli-title {
						
						a {
							color: $default-text-color;
						}
					}
				}
				
				.qodef-pli-category {
					margin: 4px 0 2px;
					line-height: 1em;
					
					a {
						color: $default-text-color;
						
						&:hover {
							color: $first-main-color;
						}
					}
				}
				
				.qodef-pli-rating-holder {
					margin: 0 0 2px;

					.qodef-pli-rating {
						color: $additional-color;
					}
				}
				
				.button,
			    .added_to_cart {
				    @include qodefButtonDefaultStyle();
				    white-space: nowrap;
				    cursor: pointer;
				    padding: 0 15px;
				    margin: -10px 0 0;
				    font-size: 13px;
				    color: $default-heading-color;
				    background-color: transparent;
				    border: 0;
				    vertical-align: middle;
				    text-align: center;
				    letter-spacing: .05em;
				    z-index: 3;

				    &:before {
                        content: '';
                        position: absolute;
                        top: calc(50% - 2px);
                        left: 8px;
                        height: 4px;
                        width: calc(100% - 16px);
                        background-color: $additional-color;
                        z-index: -1;
                        @include qodefTransitionTransform(.5s cubic-bezier(.23,1,.32,1));
                        @include qodefTransform(scaleX(0) translateZ(0));
                        @include qodefTransformOrigin(100% 50%);
                    }

				    &:hover {

					    &:before {
                            @include qodefTransformOrigin(0% 50%);
                            @include qodefTransform(scaleX(1) translateZ(0));
                        }
				    }
			    }
				
				.button.added {
					display: none;
				}
			}
			
			.qodef-pli-title,
			.qodef-pli-category a,
			.qodef-pli-excerpt,
			.qodef-pli-price {
				color: $default-heading-color;
			}
		}
	}

	&.qodef-info-below-image {

		.qodef-pli-inner {

	        .qodef-pli-text-inner {

		        .qodef-pli-add-to-cart {

			        &.qodef-light-skin {

				        .button,
				        .added_to_cart {
					        color: $default-heading-color;
					        background-color: #fff;
					        border: 1px solid #fff;

					        &:hover {
						        color: #fff;
						        background-color: $additional-color;
						        border: 1px solid $additional-color;
					        }
				        }
			        }

			        &.qodef-dark-skin {

				        .button,
				        .added_to_cart {
					        color: #fff;
					        background-color: $default-heading-color;
					        border: 1px solid $default-heading-color;

					        &:hover {
						        color: #fff;
						        background-color: $additional-color;
						        border: 1px solid $additional-color;
					        }
				        }
			        }
		        }

	            .button,
	            .added_to_cart {
		            @include qodefButtonDefaultStyle();
		            @include qodefButtonSize();
		            @include qodefButtonSolidColor();
		            white-space: nowrap;
		            z-index: 3;

		            &:hover {
			            @include qodefButtonSolidHoverColor();
			        }
	            }
	        }
	    }
	}
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: $default-heading-color;
				}
				
				.qodef-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */