/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: 67px;
    height: 27px;
    margin: 0;
    color: $default-heading-color;
    font-size: 13px;
    line-height: 27px;
    letter-spacing: 0px;
	font-weight: 700;
	text-transform: uppercase;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include qodefTransform(translateZ(0));
}

@mixin qodefWooCommerceSaleStyle() {
	top: 15px;
    left: 15px;
	color: #fff;
    background-color: $second-main-color;
}

@mixin qodefWooCommerceOutOfStockStyle() {
	top: 15px;
	left: 15px;
    background-color: #fff;
}

@mixin qodefWooCommerceNewProductStyle() {
	top: 15px;
	left: 15px;
    background-color: $additional-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    font-size: 16px;
	font-weight: 700;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 3px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
        color: $additional-color;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
	font-family: "ElegantIcons";
	font-size: 12px;
	line-height: 1;
	letter-spacing: 2px;
	color: $additional-color;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin qodefWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: baseline;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin qodefWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */