/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .qodef-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .qodef-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            padding-top: 2px;
        }

        .qodef-cart-number {
            position: absolute;
            display: inline-block;
            vertical-align: middle;
            top: calc(50% - 7px);
            right: -4px;
            width: 13px;
            height: 13px;
            font-size: 8px;
            line-height: 14px;
            font-weight: 700;
            text-align: center;
            color: $default-heading-color;
            background-color: $additional-color;
            border-radius: 100%;
            @include qodefTransform(translateY(-50%));
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-cart-icon {
                top: -1px;
                font-size: 18px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .qodef-shopping-cart-dropdown {
            opacity: 1;
            visibility: visible;
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.qodef-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 350px;
    background-color: #fff;
    border: 1px solid $default-border-color;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include qodefTransition(all .4s cubic-bezier(.23,1,.32,1));

    ul {
        margin: 30px;
        padding: 0;

        li {
            @include qodefTableLayout();
            padding: 0 0 15px;
            box-sizing: border-box;

            &.qodef-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .qodef-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 85px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .qodef-item-info-holder {
        @include qodefTableCellLayout();
        vertical-align: top;
        padding-left: 13px;

        .qodef-product-title {
            display: block;
            margin: 0;
            padding: 7px 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: -2px;
            right: 0;
	        font-size: 20px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .qodef-quantity {
            display: block;
            margin: 0;
            font-size: 12px;
        }

        .amount {
            display: block;
            margin: 0;
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase;
            color: $default-heading-color;
        }
    }

    .qodef-cart-bottom {
        margin: 11px 0 0;
        padding: 21px 0 0;
        border-top: 1px dashed $default-border-color;

        .qodef-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 16px;

            > * {
                display: table-cell;
                vertical-align: top;
                font-weight: 700;
                letter-spacing: -.025em;
                color: $default-heading-color;
                line-height: 25px;
            }

            .qodef-total-amount {
                text-align: right;
	            font-size: 16px;
                text-transform: uppercase;
            }
        }

        .qodef-view-cart {
            @include qodefButtonDefaultStyle();
            @include qodefButtonSize();
            @include qodefButtonSolidColor();
            width: 100%;
            text-align: center;
            white-space: nowrap;
            cursor: pointer;
            z-index: 3;

            &:hover {
                @include qodefButtonSolidHoverColor();
            }
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */