/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include qodefRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include qodefRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }

    &.widget_layered_nav_filters,
    &.widget_layered_nav,
    &.widget_product_categories {

        .qodef-widget-title {
            margin: 0 0 20px;
        }

        ul {

            li {
                margin: 0;
                padding: 2px 0;
                font-size: 13px;
                line-height: 25px;
                font-weight: 700;
                text-transform: uppercase;
                letter-spacing: .05em;

                a {
                    @include qodefMainHoverStyles();
                }

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 2px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 2px 0 0;
                        }
                    }
                }
            }
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: $default-heading-color;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -3px;
                    height: 9px;
                    width: 9px;
                    border-radius: 100%;
                    background-color: $additional-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include qodefTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $additional-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 33px 0 0;
            
            .button {
                float: right;
            }
            
            .price_label {
                float: left;
	            font-size: 16px;
	            font-weight: 700;
                line-height: 50px;
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .qodef-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 110px;

                .product-title {
                    font-family: $default-heading-font;
                    font-size: 14px;
                    font-weight: 700;
                    color: $default-heading-color;
                }
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    line-height: 1;
                    color: $additional-color;
                    
                    span {
                        max-width: 100px;
                    }
                }

                .amount {
                    font-family: $default-heading-font;
                    font-size: 14px;
                    font-weight: 700;
                    color: $default-heading-color;
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: block;
            clear: both;
        }
    }
    
    &.widget_product_tag_cloud {

        .qodef-widget-title {
            margin: 0 0 18px;
        }

        .tagcloud {

            a {
                display: inline-block;
                vertical-align: middle;
                margin-right: 7px;
                font-family: inherit;
                font-size: 13px !important;
                line-height: 34px;
                font-weight: 700;
                text-transform: uppercase;
                letter-spacing: .05em;
                background-color: transparent;
                @include qodefMainHoverStyles();
            }
        }
    }
    
    &.widget_product_search {

        .qodef-widget-title {
            margin: 0 0 18px;
        }

        .woocommerce-product-search {
            @include qodefRelativeHolderLayout();
            clear: both;
            
            > div {
                @include qodefRelativeHolderLayout();
                border-bottom: 1px solid $default-heading-color;
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                height: 52px;
                font-family: inherit;
                font-weight: 400;
                font-size: 14px;
                line-height: 66px;
                color: $default-heading-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include qodefTransition(color .2s ease-out);
            }

            input {
                width: calc(100% - 28px);
                padding: 10px 0;

                @include qodefPlaceholder {
                    color: inherit;
                }
            }

            button {
                width: 28px;
                padding: 10px 0;
                font-size: 18px;
                text-align: right;
                color: $default-heading-color;
                cursor: pointer;
                @include qodefTransition(color .2s ease-in-out);

                &:hover {
                    color: $first-main-color;
                }

                span {
                    display: block;

                    &:before {
                        display: block;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */